body {
    background-color: #e7ecf2;
  }
  
  .profile * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Lato';
    font-size: 14px;
    font-weight: 400;
  }
  
  .profile {
    position: relative;
    margin: 10px;
    text-align: center;
    border-radius: 4px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 1px 3px 0px rgba(191, 205, 222, 0.75);
    width: 250px;
    height: 330px;
  }
  
  .description {
    line-height: 28px;
    font-size: 14px;
    color: rgb(118, 134, 150);
  }
  .avatar {
    margin: 30px 0 10px 0;
    border-radius: 50%;
    width: 100px;
    height: 100px;
  }
  
  .name {
    font-size: 18px;
    color: rgb(18, 34, 54);
    font-weight: bold;
  }
  
  .stats {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    bottom: 0;
    width: 100%;
    height: 74px;
    list-style: none;
    border-style: solid;
    border-width: 1px;
    border-color: rgb(228, 233, 240);
    background-color: rgb(243, 246, 249);
  }
  
  .stats li {
    padding: 15px 0;
    width: 33.33333%;
  }
  
  .stats li:hover {
    background-color: rgb(255, 255, 255);
    cursor: pointer;
  }
  
  .stats li:not(:last-child) {
    border-right: 1px solid rgb(228, 233, 240);
  }
  
  .label,
  .quantity {
    line-height: 20px;
  }
  
  .label {
    display: block;
    margin: 0 auto;
    font-size: 12px;
  
    color: rgb(118, 134, 150);
  }
  
  .quantity {
    margin: 0 auto;
    color: rgb(31, 51, 73);
    font-weight: 800;
  }